@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap");
*,
*:after,
*:before {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-decoration: none;
}

:root {
  --font-default: "Roboto Condensed", serif;
  --bg-color: #E8E7E7;
  --default-padding: 20px;
  --order-status-recusado: #BD1818;
  --order-status-pendente: #FF735B;
  --order-status-aprovado: #3471CD;
  --order-status-faturado: #08B26B;
}

section {
  font-family: "Roboto Condensed", serif;
}
section .header {
  display: grid;
  grid-template-areas: "back title new";
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 20px;
  position: relative;
  z-index: 20;
}
section .header > div.back {
  grid-area: back;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 18px;
  color: #818181;
  gap: 10px;
}
section .header > h1 {
  grid-area: title;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: #44464E;
  text-transform: uppercase;
}
section .header div.new-order {
  grid-area: new;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 18px;
  color: #818181;
}
section .search-bar {
  position: relative;
  margin-top: 20px;
  border-bottom: 1px solid #cbcbcb;
  padding-bottom: 15px;
}
section .search-bar input {
  background-color: #8E9098;
  color: #F6F6F6;
  font-family: var(--font-default);
  font-size: 16px;
  border: none;
  padding: 7px;
  width: 100%;
  border-radius: 5px;
  font-weight: 400;
  outline: none;
}
section .search-bar input::placeholder {
  color: #fff;
}
section .search-bar button {
  position: absolute;
  right: 10px;
  top: 30%;
  transform: translateY(-30%);
  border: none;
  background-color: transparent;
}
section .search-bar button svg {
  stroke: #fff;
}
section .search-bar button svg circle, section .search-bar button svg path {
  stroke: #fff;
}
section .toggle-area {
  background-color: #D9D9D9;
  padding: 4px;
  border-radius: 5px;
  margin-top: 20px;
}
section .toggle-area .toggle {
  display: flex;
}
section .toggle-area .toggle > .toggle-item {
  width: 100%;
  text-align: center;
  line-height: 1.8;
  color: #44464E;
  font-weight: 500;
  border-radius: 5px;
  transition: all 0.2s ease-out;
}
section .toggle-area .toggle > .toggle-item.active {
  background-color: #F6F6F6;
}
section .status-list {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
section .status-list > span {
  display: flex;
  text-align: center;
  color: #44464E;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  gap: 2px;
}
section .order-area .date {
  display: block;
  padding: 10px 20px;
  font-size: 13px;
  color: #44464E;
  font-weight: 300;
}
section .order-area .orders {
  margin-bottom: 10px;
}
section .order-area .orders .order {
  background-color: #F6F6F6;
  padding: 15px 20px;
}
section .order-area .orders .order.recusado .order-header .status {
  color: var(--order-status-recusado);
}
section .order-area .orders .order.aprovado .order-header .status {
  color: var(--order-status-aprovado);
}
section .order-area .orders .order.pendente .order-header .status {
  color: var(--order-status-pendente);
}
section .order-area .orders .order.faturado .order-header .status {
  color: var(--order-status-faturado);
}
section .order-area .orders .order:not(:last-child) {
  margin-bottom: 10px;
}
section .order-area .orders .order .order-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #d0d0d0;
  padding-bottom: 10px;
}
section .order-area .orders .order .order-header > div {
  display: flex;
  align-items: center;
  gap: 5px;
}
section .order-area .orders .order .order-header .status {
  font-size: 13px;
  font-weight: bold;
}
section .order-area .orders .order .order-header .details {
  display: flex;
  gap: 10px;
}
section .order-area .orders .order .order-header .details .user {
  color: #9A9A9A;
  font-weight: 400;
  font-size: 12px;
}
section .order-area .orders .order .order-details {
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
section .order-area .orders .order .order-details .business-name {
  display: flex;
  gap: 5px;
  color: #44464E;
  font-weight: 500;
  font-size: 13px;
}
section .order-area .orders .order .order-details .business-name > span {
  text-transform: uppercase;
  display: block;
}
section .order-area .orders .order .order-details .totals {
  color: #44464E;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 5px;
}
section .filters {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}
section .filters > select {
  width: 100%;
  padding: 5px 5px;
  border-radius: 5px;
  border-color: #616471;
  background-color: transparent;
  font-family: var(--font-default);
  color: #616471;
  font-size: 12px;
  font-weight: 500;
  outline: none;
}
section .filters button {
  background-color: #8E9098;
  border-radius: 5px;
  border: none;
  padding: 5px 10px;
}
section .list-products .product {
  display: grid;
  grid-template-columns: 75% 1fr;
  background-color: #fff;
  padding: 0;
  margin-bottom: 5px;
}
section .list-products .product .details {
  display: flex;
  border-right: 1px solid #818181;
}
section .list-products .product .details img {
  margin-top: 5px;
  width: 65px;
  object-fit: contain;
}
section .list-products .product .details > div {
  margin-left: 5px;
  padding-top: 10px;
  position: relative;
  width: 100%;
}
section .list-products .product .details > div h4 {
  color: #44464E;
  font-size: 15px;
}
section .list-products .product .details > div p {
  font-size: 12px;
  color: #44464E;
}
section .list-products .product .details > div .current-quantity {
  background-color: #e8e7e7;
  position: absolute;
  right: 0;
  bottom: 0;
  width: 55px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: #44464E;
  font-weight: 500;
}
section .list-products .product .price {
  position: relative;
}
section .list-products .product .price strong {
  color: #2D2DB0;
  font-size: 17px;
  font-weight: 500;
  display: block;
  padding: 10px 5px;
}
section .list-products .product .price .qtd-toggle {
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
section .list-products .product .price .qtd-toggle > button {
  width: 100%;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
}
section .list-products .product .price .qtd-toggle > button.minus {
  background-color: #D9D9D9;
}
section .list-products .product .price .qtd-toggle > button.plus {
  background-color: #BEBEBE;
}
section .navigation {
  position: fixed;
  bottom: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px var(--default-padding);
  background-color: #fff;
  width: 100%;
}
section .navigation button {
  color: #44464e;
  font-size: 8px;
  font-weight: 500;
  border: none;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
  width: 48px;
  height: 48px;
  transition: all 0.2s ease-out;
}
section .navigation button svg {
  height: 29px;
}
section .navigation button.active {
  background-color: #D9D9D9;
  border-radius: 3px;
}
section.login {
  background: url(../img/bg-login.png), #E8E7E7;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100vh;
  position: relative;
}
section.login .wrap {
  text-align: center;
  padding-top: 20px;
}
section.login .wrap > img {
  width: 168px;
}
section.login .wrap form {
  background-color: rgba(255, 255, 255, 0.5);
  max-width: 85%;
  margin: 0 auto;
  margin-top: 28px;
  border-radius: 10px;
  padding: 15px 40px;
  position: relative;
}
section.login .wrap form::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(3px);
  left: 0;
  top: 0;
  z-index: 0;
  border-radius: 10px;
}
section.login .wrap form > div.mb-3:not(:last-child) {
  margin-bottom: 20px;
}
section.login .wrap form > div, section.login .wrap form > button, section.login .wrap form > a {
  z-index: 1;
  position: relative;
  font-family: var(--font-default);
}
section.login .wrap form > div input, section.login .wrap form > button input, section.login .wrap form > a input {
  font-family: var(--font-default);
  font-size: 14px;
  width: 100%;
  border: none;
  box-sizing: border-box;
  background-color: transparent;
  border-bottom: 1px solid #44464E;
  color: #44464E;
  font-weight: 400;
  outline: none;
}
section.login .wrap form > div input::placeholder, section.login .wrap form > button input::placeholder, section.login .wrap form > a input::placeholder {
  color: #44464E;
}
section.login .wrap form button {
  display: block;
  width: 100%;
  background-color: #44464E;
  color: #E8E7E7;
  border: none;
  border-radius: 3px;
  line-height: 33px;
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 20px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  height: 33px;
}
section.login .wrap form > a {
  display: block;
  text-align: center;
  color: #44464E;
  text-decoration: underline;
  font-size: 13px;
}
section.login .footer {
  position: absolute;
  bottom: 20px;
  left: 0;
  width: 100%;
  color: #FFFFFF;
  font-size: 12px;
  margin: 0 auto;
  text-align: center;
  padding: 0 40px;
}
section.login .footer.hidden {
  display: none;
}
section.login .footer span:not(.red) {
  color: #312783;
}
section.login .footer span.red {
  color: #BD1818;
}
section.loading {
  background: url(../img/loading-bg.png), #e8e7e7;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom;
  height: 100vh;
  position: relative;
}
section.loading .loading-progress {
  position: absolute;
  width: 100%;
  left: 0;
  top: 40%;
  padding: 0 40px;
  box-sizing: border-box;
}
section.loading .loading-progress .progress {
  background-color: #9A9A9A;
  border-radius: 10px;
  height: 17px;
  transition: width 0.5s ease-in-out;
}
section.loading .loading-progress .progress .percent {
  height: 17px;
  background-color: #312783;
  border-radius: 10px;
  transition: all 0.2s ease-out;
  position: relative;
}
section.loading .loading-progress .progress .percent img {
  transition: all 0.2s ease-out;
  position: absolute;
  top: -35px;
  left: 100%;
  transform: translateX(-100%);
  width: 40px;
}
section.orders {
  background-color: var(--bg-color);
  height: calc(100dvh - 70px);
  max-height: calc(100dvh - 70px);
  overflow: auto;
}
section.orders .wrap {
  padding: 20px;
}
section.clients {
  background-color: var(--bg-color);
  height: calc(100dvh - 70px);
  max-height: calc(100dvh - 70px);
  overflow: auto;
  padding: 20px;
}
section.clients .search-bar {
  border-bottom: 0;
}
section.clients .clients-area {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  position: relative;
  margin-top: 15px;
}
section.clients .clients-area .az-list {
  position: fixed;
  top: 20%;
  right: 5px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  z-index: 1;
  justify-content: center;
}
section.clients .clients-area .az-list .letter {
  font-size: 10px;
  font-weight: 500;
  color: #44464E;
  text-align: center;
}
section.clients .clients-area .clients-list {
  width: 100%;
}
section.clients .clients-area .clients-list .prefix {
  display: block;
  padding: 10px 0;
  font-size: 14px;
  color: #44464E;
  font-weight: 500;
}
section.clients .clients-area .clients-list .list .client {
  border-bottom: 1px solid #cbcbcb;
  width: 100%;
  padding-bottom: 10px;
}
section.clients .clients-area .clients-list .list .client:not(:last-child) {
  margin-bottom: 10px;
}
section.clients .clients-area .clients-list .list .client h4 {
  color: #2D2DB0;
  font-size: 14px;
  font-weight: 500;
}
section.clients .clients-area .clients-list .list .client p, section.clients .clients-area .clients-list .list .client div {
  font-size: 14px;
  color: #44464E;
  font-weight: 400;
}
section.new-order {
  background-color: var(--bg-color);
  height: calc(100dvh - 70px);
  max-height: calc(100dvh - 70px);
  overflow: auto;
}
section.new-order .header {
  padding: var(--default-padding);
}
section.new-order .choose-client {
  background-color: #F6F6F6;
  padding: 10px var(--default-padding);
  display: flex;
  gap: 10px;
  position: relative;
}
section.new-order .choose-client h4 {
  color: #44464E;
  font-size: 20px;
  font-weight: 400;
  line-height: 1;
}
section.new-order .choose-client p {
  font-size: 10px;
  color: #44464E;
  font-weight: 300;
}
section.new-order .choose-client .arrow {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
section.new-order .details {
  padding: 15px var(--default-padding);
  display: flex;
}
section.new-order .details > div {
  width: 100%;
  text-align: center;
  color: #616471;
  font-size: 13px;
  font-weight: 500;
}
section.new-order .products {
  padding: 0 var(--default-padding);
  margin-bottom: 20px;
}
section.new-order .products button.add-product {
  border-radius: 4px;
  padding: 8px 10px;
  text-align: center;
  border: none;
  background-color: #8E9098;
  color: #F6F6F6;
  width: 100%;
  font-family: var(--font-default);
  text-transform: uppercase;
  font-size: 15px;
  font-weight: 400;
  outline: none;
}
section.new-order .info {
  margin-bottom: 10px;
}
section.new-order .info h2 {
  line-height: 1.4;
  font-size: 15px;
  color: #44464E;
  text-transform: uppercase;
  font-weight: 500;
  padding: 0 var(--default-padding);
  margin-bottom: 5px;
}
section.new-order .info ul {
  background-color: #F6F6F6;
  padding: 6px var(--default-padding);
}
section.new-order .info ul li {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  color: #616471;
  font-size: 15px;
  font-weight: 300;
  line-height: 1.8;
}
section.new-order .info ul li:not(:last-child) {
  border-bottom: 1px solid #cbcbcb;
}
section.new-order .info ul li .arrow {
  position: relative;
  right: -5px;
}
section.new-order .info textarea {
  color: #44464E;
  border: none;
  width: 100%;
  border-radius: 0;
  padding: 10px var(--default-padding);
  box-sizing: border-box;
  font-family: var(--font-default);
  outline: none;
}
section.new-order .info textarea::placeholder {
  color: #44464E;
}
section.new-order .ctas {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  padding: var(--default-padding);
  margin-bottom: 10px;
}
section.new-order .ctas button {
  padding: 10px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-family: var(--font-default);
  border: none;
  border-radius: 3px;
  gap: 10px;
  text-align: center;
}
section.new-order .ctas button.send {
  width: 38%;
  background-color: #06985C;
  font-weight: 500;
  font-size: 15px;
}
section.new-order .ctas button.save {
  width: 56%;
  background-color: #F5C64B;
  font-weight: 500;
  font-size: 15px;
}
section.new-order .ctas button.send-email, section.new-order .ctas button.duplicate, section.new-order .ctas button.cancel {
  flex-direction: column;
  gap: 5px;
  font-size: 10px;
  background-color: #616471;
  width: 29%;
  padding: 5px 0;
}
section.new-order .ctas button.cancel {
  background-color: #BD1818;
}
section.new-order .cancel-popup {
  position: fixed;
  z-index: 17;
  left: 0;
  top: 0;
  width: 100%;
  max-height: calc(100dvh - 70px);
  height: calc(100dvh - 70px);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
section.new-order .cancel-popup:not(.active) {
  display: none;
}
section.new-order .cancel-popup .overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(217, 217, 217, 0.85);
  z-index: 1;
}
section.new-order .cancel-popup .content {
  position: relative;
  z-index: 2;
}
section.new-order .cancel-popup .content p {
  font-family: var(--font-default);
  color: #44464E;
  font-size: 24px;
  max-width: 70%;
  margin: 0 auto;
  font-weight: 500;
  margin-bottom: 20px;
}
section.new-order .cancel-popup .content button {
  background-color: #BD1818;
  color: #E8E7E7;
  border: none;
  width: 160px;
  margin: 0 auto;
  line-height: 2.5;
  border-radius: 3px;
  text-transform: uppercase;
  font-family: var(--font-default);
  font-size: 15px;
}
section.client-selected {
  background-color: var(--bg-color);
  height: calc(100dvh - 70px);
  max-height: calc(100dvh - 70px);
  overflow: auto;
}
section.client-selected .header {
  padding: var(--default-padding);
}
section.client-selected .ctas {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--default-padding);
  padding: 10px 30px;
  margin-bottom: 10px;
}
section.client-selected .ctas button {
  width: 100%;
  font-family: var(--default-padding);
  color: #44464E;
  background-color: #D9D9D9;
  border: none;
  border-radius: 4px;
  height: 47px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
section.client-selected .info {
  margin-bottom: 10px;
}
section.client-selected .info h2 {
  line-height: 1.4;
  font-size: 15px;
  color: #44464E;
  text-transform: uppercase;
  font-weight: 500;
  padding: 0 var(--default-padding);
  margin-bottom: 10px;
}
section.client-selected .info ul {
  background-color: #F6F6F6;
  padding: 6px var(--default-padding);
}
section.client-selected .info ul li {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  gap: 5px;
  color: #616471;
  font-size: 15px;
  font-weight: 300;
  line-height: 1;
  font-size: 16px;
  padding: 6px 0;
}
section.client-selected .info ul li:not(:last-child) {
  border-bottom: 1px solid #cbcbcb;
}
section.client-selected .info ul li span {
  font-weight: 400;
  color: #44464E;
  line-height: 1;
}
section.client-selected .info ul li span.name {
  color: #2D2DB0;
  font-weight: bold;
  font-size: 18px;
}
section.client-selected .info ul li .arrow {
  position: absolute;
  right: -5px;
}
section.client-selected .info textarea {
  color: #44464E;
  border: none;
  width: 100%;
  border-radius: 0;
  padding: 10px var(--default-padding);
  box-sizing: border-box;
  font-family: var(--font-default);
  outline: none;
}
section.client-selected .info textarea::placeholder {
  color: #44464E;
}
section.client-selected .info.contact-data {
  margin-bottom: 0;
}
section.client-selected .info.contact-data h2 {
  margin-bottom: 5px;
}
section.client-selected .info.contact-data li {
  font-weight: 400;
  color: #44464E;
}
section.client-selected iframe {
  height: 222px;
  margin-bottom: 5px;
  width: 100%;
}
section.new-client {
